import React from "react";
import {Row, Col, Table, Button} from "reactstrap";
import {image} from "faker";
import {Link} from "react-router-dom";

import "./MainContent.css";
import "bootstrap/dist/css/bootstrap.min.css";  
const MainContent = () => {
    return (
        <Table className="table-main text-white m-0" style={{height:"100vh", width:"100%"}}>
            <tr>
                <td className="content-data justify-content-center align-middle ">
                    <h1 className="main-content-title pb-3 mx-auto" >The Best Asian Cuisine is served here </h1>
                    <div className="divider bg-light m-auto"></div>
                    <p className="content-description mx-auto mt-3 mb-5">
                        Join us and enjoy your dinner in a friendly environment full of friendly people and beautiful Ethnic dishes.
                    </p>
                   
                    <button><Link className="text-white" to="/Menu">See Menu</Link></button>

                </td>
            </tr>
        </Table>
        
    )
}

export default MainContent;
