import React from "react";
import "./Menu.css";
import {image,commerce, lorem } from "faker";
import "bootstrap/dist/css/bootstrap.min.css";
import {} from "reactstrap";

import Header from "../../Components/Header/Header";
import PageHome from "../../Components/PageHome/PageHome";
import Appetizer from "../../Components/Appetizer/Appetizer";
import Footer from "../../Components/Footer/Footer";
import Follow from "../../Components/Follow/Follow";
import Specials from "../../Components/Specials/Specials";
import Meals from "../../Components/Meals/Meals";
import Desserts from "../../Components/Desserts/Desserts";
import Drinks from "../../Components/Drinks/Drinks";



const Menu = () => {
    const title = "Our Menu";
    const description = "Check out what food we offer";
    return(
        <div>
            <Header />
            <PageHome title={title} description={description} />
            <Appetizer />
            <Specials />
            <Meals />
            <Desserts />
            <Drinks />
            <Follow />
            <Footer />
        </div>
    )
}

export default Menu;

