import React from "react";
import {Row, Col, Card, CardBody, CardText, CardTitle,CardImg, Button} from "reactstrap";
import {image, random, commerce} from "faker";
import {Link} from "react-router-dom";
import "./BestSeller.css";
import "bootstrap/dist/css/bootstrap.min.css"; 

const BestSeller = () => {
    return (
        <div className="bg-dark py-5">
            <Row className="justify-content-center mx-md-0 mx-5">
                <Col md={3} className="">
                    <h2 className="special-title text-md-left text-center text-white">Our Specials</h2>
                    <div className="py-2">
                        <div className="divider mx-md-0 mx-auto" style={{width:"80px", height:"1px", backgroundColor:"#E8290B"}}></div>
                    </div>
                    <div className="py-2">
                        <p className="special-description text-md-left text-white mx-auto">Our chef is preparing fresh dishes every day, check what dish our costumers are satisfied for!</p>
                    </div>
                    <div className="pt-2 pb-4 ">
                        <button className="float-md-left text-white mx-md-0 p-2 mx-auto" style={{backgroundColor:"#E8290B"}}><Link className="text-white" to={{pathname:"/Menu", hash:"#"}}>Check Specials</Link></button>
                    </div>
                </Col>
                <Col md={3} className="my-md-0 my-1">
                    <Card className="" style={{height:"100%"}} >
                        <CardImg className="p-sm-5 p-md-3 p-3" top width="100%" src={image.food()} alt="Card image cap" />
                        <CardBody>
                            <CardTitle>Kung Pao Brussels Sprouts</CardTitle>
                            <CardText className="text-dark" style={{fontWeight:"bold", fontSize:"25px"}}>$ {commerce.price()}</CardText>
                            <CardText>The kung pao sauce on these will turn everyone into a lover of Brussels sprouts.</CardText>
                        </CardBody>
                    </Card>
                </Col>
                <Col md={3}>
                    <Card className="my-md-0 my-1" style={{height:"100%"}}>
                            <CardImg className="p-sm-5 p-md-3 p-3" top width="100%" src={image.food()} alt="Card image cap" />
                            <CardBody>
                                <CardTitle>Sweet and Sour Chicken</CardTitle>
                                <CardText className="text-dark" style={{fontWeight:"bold", fontSize:"25px"}}>$ {commerce.price()}</CardText>
                                <CardText>It's the combo you can't get enough of.</CardText>
                            </CardBody>
                    </Card>
                </Col>
            </Row>
        </div>
    )
}

export default BestSeller;