import React from "react";
import "./Gallery.css";

import {Row, Col} from "reactstrap";

import "bootstrap/dist/css/bootstrap.min.css";
import {image} from "faker";

const Gallery = () => {

    return (
        <div className="Gallery bg-dark mt-5 pb-5">
            <h4 className="pt-5 text-white">Our Photo Gallery</h4>
            <p className="text-white "> Get Inspired and get ready!</p>
            <div className="divider mx-auto mt-3" style={{width:"50px", height:"1px", backgroundColor:"#E8290B"}}></div>
            <div className="mt-3 mx-5">
                <Row className="justify-content-center mx-sm-5">
                    <Col className="p-2 col-6" lg={2} md={3} sm={6}>
                        <img src={image.food()}  style={{width:"100%"}} />
                    </Col>
                    <Col className="p-2 col-6" lg={2} md={3} sm={6}>
                        <img src={image.food()}  style={{width:"100%"}} />
                    </Col >
                    <Col className="p-2 col-6" lg={2} md={3} sm={6}>
                         <img src={image.food()}  style={{width:"100%"}} />
                    </Col>
                    <Col className="p-2 col-6" lg={2} md={3} sm={6}>
                        <img src={image.food()}  style={{width:"100%"}} />
                    </Col>
                </Row>
                <Row className="justify-content-center mx-sm-5 ">
                    <Col className="p-2 col-6" lg={2} md={3} sm={6}>
                        <img src={image.food()}  style={{width:"100%"}} />
                    </Col>
                    <Col className="p-2 col-6" lg={2} md={3} sm={6}>
                        <img src={image.food()}  style={{width:"100%"}} />
                    </Col >
                    <Col className="p-2 col-6" lg={2} md={3} sm={6}>
                         <img src={image.food()}  style={{width:"100%"}} />
                    </Col>
                    <Col className="p-2 col-6" lg={2} md={3} sm={6}>
                        <img src={image.food()}  style={{width:"100%"}} />
                    </Col>
                </Row>
            </div>
        </div>
    )
}

export default Gallery;