import React from "react";

import { Row, Col, Card, CardBody, CardText, CardTitle, CardImg } from "reactstrap";
import {image, commerce} from "faker";

const Specials = () => {
    return(
       <div className="pt-5" id="#specials">
            <div className="my-3">
                <h3 className="text-white text-center">Our Specials</h3>
            </div>
            <Row className="justify-content-center mx-lg-4 mx-md-5 mx-sm-4" noGutters>
                <Col sm={4} lg={3} className="my-2 col-10">
                    <Card className="mx-1" style={{height:"100%"}} >
                        <CardImg className="p-1" top width="100%" src={image.food()} alt="Card image cap" />
                        <CardBody>
                            <CardTitle>Sweet and Sour Chicken</CardTitle>
                            <CardText className="text-dark" style={{fontWeight:"bold", fontSize:"13px"}}>$ {commerce.price()}</CardText>
                            <CardText>It's the combo you can't get enough of.</CardText>
                        </CardBody>
                    </Card>
                </Col>
                <Col sm={4} lg={3} className="my-2 col-10">
                    <Card className="mx-1" style={{height:"100%"}} >
                        <CardImg className="p-1" top width="100%" src={image.food()} alt="Card image cap" />
                        <CardBody>
                            <CardTitle>Sweet and Sour Chicken</CardTitle>
                            <CardText className="text-dark" style={{fontWeight:"bold", fontSize:"13px"}}>$ {commerce.price()}</CardText>
                            <CardText>It's the combo you can't get enough of.</CardText>
                        </CardBody>
                    </Card>
                </Col>
                <Col sm={4} lg={3} className="my-2 col-10">
                    <Card className="mx-1" style={{height:"100%"}} >
                        <CardImg className="p-1" top width="100%" src={image.food()} alt="Card image cap" />
                        <CardBody>
                            <CardTitle>Sweet and Sour Chicken</CardTitle>
                            <CardText className="text-dark" style={{fontWeight:"bold", fontSize:"13px"}}>$ {commerce.price()}</CardText>
                            <CardText>It's the combo you can't get enough of.</CardText>
                        </CardBody>
                    </Card>
                </Col>
            </Row>
       </div>
    )   
}

export default Specials;