import React from 'react';
import {
  MDBContainer,
  MDBCarousel,
  MDBCarouselInner,
  MDBCarouselItem,
  MDBTestimonial,
  MDBAvatar,
  MDBIcon
} from 'mdbreact';
import "bootstrap/dist/css/bootstrap.min.css";
import {image, lorem, name} from "faker";
import {FaQuoteLeft, FaStar} from "react-icons/fa"

const Testimonials = ({heading, divider}) => {
  return (
        <div className="mt-5 text-white">
            <div>
                {heading ? (<h4>{heading}</h4>) : null}
                {divider ? (<div className="divider m-auto " style={{width:"50px", height:"1px", backgroundColor:"#E8290B"}}></div>): null}
            </div>
            <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel" style={{width:"100%", height:"Auto" }} >
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <div className="mx-auto" style={{width:"70%"}}>
                            <img 
                                src = {image.avatar()}
                                className="rounded-circle my-3"
                                alt="avatar"
                                style={{width:"100px", height:"100px"}}
                            />
                            <p className="text-center"><span><FaQuoteLeft className="pr-1" /></span>{lorem.paragraph()}</p>
                            <span>{`${name.firstName()} ${name.lastName()}`}</span>
                            <div>
                                <FaStar className="text-warning" />
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                            </div>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <div className="mx-auto" style={{width:"70%"}}>
                            <img 
                                src = {image.avatar()}
                                className="rounded-circle my-3"
                                alt="avatar"
                                style={{width:"100px", height:"100px"}}
                            />
                            <p className="text-center"><span><FaQuoteLeft className="pr-1" /></span>{lorem.paragraph()}</p>
                            <span>{`${name.firstName()} ${name.lastName()}`}</span>
                            <div>
                                <FaStar className="text-warning" />
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                            </div>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <div className="mx-auto" style={{width:"70%"}}>
                            <img 
                                src = {image.avatar()}
                                className="rounded-circle my-3"
                                alt="avatar"
                                style={{width:"100px", height:"100px"}}
                            />
                            <p className="text-center"><span><FaQuoteLeft className="pr-1" /></span>{lorem.paragraph()}</p>
                            <span>{`${name.firstName()} ${name.lastName()}`}</span>
                            <div>
                                <FaStar className="text-warning" />
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                                <FaStar className="text-warning"/>
                            </div>
                        </div>
                    </div>
                </div>
                <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon bg-dark" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                </a>
                <a className="carousel-control-next " href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon bg-dark" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                </a>
            </div>
        </div>

  );
};

export default Testimonials;