import React from "react";
import {image} from "faker";

import "./PageHome.css";
import { Row, Col } from "reactstrap";
import "bootstrap/dist/css/bootstrap.min.css";

const PageHome = ({title, description}) => {
    return (
        <div className="page-home bg-light align-items-middle">
            <table className="table-section align-items-middle">
                <tbody className="table-section align-items-middle">
                    <tr>
                        <td className="align-bottom"><h1 className="text-white">{title}</h1></td>
                    </tr>
                    <tr>
                        <td className="align-top"><h3 className="text-white">{description}</h3></td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}

export default PageHome;

