import React from "react";
import { Row, Col } from "reactstrap";
import {image, lorem} from "faker";
import "./History.css";

const History = () => {
    return(
        <div className="py-5">
            <div className="text-white">
                <Row className="justify-content-center">
                    <Col sm={10} className="col-10">
                        <h3 className="text-center" >A place with a history worth sharing</h3>
                        <p className="history-description text-justify">{lorem.paragraphs()}</p>
                    </Col>   
                </Row>
            </div>
            <div>
                <Row className="justify-content-center mx-3 pt-2" noGutters>
                    <Col sm={3} className="col-12 m-1">
                        <Row className="justify-content-center" noGutters>
                            <Col sm={12} className="col-5 m-1">
                                <img src={image.food()} style={{width:"100%", height:"100%"}} />
                            </Col>
                            <Col sm={12} className="col-5 m-1">
                                <img src={image.food()} style={{width:"100%", height:"100%"}} />
                            </Col> 
                        </Row>
                    </Col>

                    <Col sm={3} className="col-12 m-1">
                        <Row className="justify-content-center" noGutters>
                            <Col sm={12} className="col-5 m-1">
                                <img src={image.food()} style={{width:"100%", height:"100%"}} />
                            </Col>
                            <Col sm={12} className="col-5 m-1">
                                <img src={image.food()} style={{width:"100%", height:"100%"}} />
                            </Col>
                        </Row>
                    </Col>
                    
                    <Col className="m-2 col-10" sm={4}> 
                         <img className="" src={image.food()}  style={{width:"100%", height:"100%"}}  />
                    </Col>
                </Row>
            </div>
        </div>
    )
}

export default History;