import React from "react";
import {Row, Col, Table, Button} from "reactstrap";
import {FaPhone, FaMapMarker} from "react-icons/fa";
import "./Info.css";
const Info = () => {
    return (
        <div className="info mx-auto my-5 position-absoulte">
            <Row className="justify-content-center ">
                <Col className="m-1 pt-4" >
                    <h4 className="text-white mb-3">Come visit us at the centre of the city!</h4>
                    <div className="m-1"><FaMapMarker className="mr-2" size={20} color="white"/><a className="text-white" target="blank" href="https://www.google.ca/maps/place/CF+Toronto+Eaton+Centre/@43.6544421,-79.3828881,17z/data=!3m1!4b1!4m5!3m4!1s0x882b34cb510746bd:0x8b89147b8cbbc837!8m2!3d43.6544382!4d-79.3806994">
                        220 Yonge St, Toronto, ON M5B 2H1
                        </a>
                    </div>
                    <div className="m-1"><FaPhone className="mr-2" size={20} color="white" /><a className="text-white" href="tel:647-656-7678" >(647)-656-7678</a></div>
                </Col>
            </Row>
        </div>
        
    )
}

export default Info;
