import React, { useState } from 'react';
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink } from 'reactstrap';
import {Link} from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./Header.css";

const Header = () => {
  const [collapsed, setCollapsed] = useState(true);

  const toggleNavbar = () => setCollapsed(!collapsed);

  return (
      <Navbar className="Nav-bar fixed-top" style={{backgroundColor:"white"}} light expand="md">
        <NavbarBrand className="mr-auto"><Link className="text-dark" to="/">MyRestaurant</Link></NavbarBrand>
        <NavbarToggler onClick={toggleNavbar} className="mr-2" />
        <Collapse className="justify-content-center" isOpen={!collapsed} navbar>
          <Nav className="text-center" navbar>
            <NavItem>
              <NavLink className="mx-3" ><Link className="text-dark" to="/Menu">Menu</Link></NavLink>
            </NavItem>
            <NavItem>
              <NavLink className="mx-3" ><Link className="text-dark" to="/Reservation">Reservation</Link></NavLink>
            </NavItem>
            <NavItem>
              <NavLink className="mx-3" ><Link className="text-dark" to="/About">Our Restaurant</Link></NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
  )
}

export default Header;