import React from "react";
import "./Follow.css";
import {FaTwitterSquare, FaFacebookSquare, FaInstagramSquare, FaLinkedin} from "react-icons/fa";

const Follow = () => {
    return (
        <div className="Follow text-white mb-5">
            <h5 className="text-center mt-5">Please follow us on our Social Links</h5>
            <p>Get exclusive deals, offers and rewards!</p>
            <div>
                <a href="#"><span><FaFacebookSquare size={25} className="m-1" style={{color:"#FF4848"}}  /></span></a>
                <a href="#"><span><FaTwitterSquare size={25} className="m-1" style={{color:"#FF4848"}}  /></span></a>
                <a href="#"><span><FaInstagramSquare size={25} className="m-1" style={{color:"#FF4848"}}  /></span></a>
                <a href="#"><span><FaLinkedin size={25} className="m-1" style={{color:"#FF4848"}}  /></span></a>
            </div>
        </div>
    )
}

export default Follow;